*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  color: inherit;
  height: 0;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button {
  height: auto;
}

::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

@font-face {
  font-family: Neue Montreal;
  src: url("NeueMontreal-Medium.4f251081.woff2") format("woff2"), url("NeueMontreal-Medium.f9be302d.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Fonts Pring;
  src: url("FONTSPRINGDEMO-QuincyCFBlackRegular.e3fb1854.woff2") format("woff2"), url("FONTSPRINGDEMO-QuincyCFBlackRegular.3f03c2bb.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Neue Montreal;
  src: url("NeueMontreal-Regular.b86b28d3.woff2") format("woff2"), url("NeueMontreal-Regular.24747728.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

.visually-hidden {
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

:root {
  --color-main-bg: #e8e8e2;
  --color-second-bg: #6d8434;
  --third-bg: #fafafa;
  --color-text: #010101;
  --color-second-text: #a3a3a3;
  --color-btn-green-dark: #586f1f;
  --color-btn-gray: #e8e8e2;
  --color-btn-black: #010101;
  --color-text-transparent: #0101014d;
  --color-text-transparent-second: #010101b3;
}

body {
  color: var(--color-text);
  background-color: var(--color-main-bg);
  font-family: Neue Montreal, sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
}

.container {
  max-width: 375px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 100px;
    padding-right: 100px;
  }
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  font-family: inherit;
  text-decoration: none;
}

button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  font-family: inherit;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

svg {
  display: block;
}

