@media screen and (min-width: 428px) {
  .header {
    padding-top: 18px;
  }

  .header-list {
    justify-content: space-between;
    display: flex;
  }

  .header-item {
    text-align: center;
    align-items: center;
    display: flex;
  }

  .header-item.first {
    display: block;
  }

  .header-logo-link {
    justify-content: center;
    align-items: center;
  }

  span#cart-counter {
    width: 30px;
  }

  .icon-svg {
    background-color: var(--color-second-bg);
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    width: 28px;
    height: 28px;
    display: flex;
  }

  .logo-icon {
    fill: var(--color-main-bg);
    width: 16px;
    height: 16px;
  }

  .header-logo {
    color: var(--color-text);
    letter-spacing: .02em;
    justify-content: center;
    align-items: center;
    font-family: Fonts Pring;
    font-size: 18px;
    font-weight: 900;
    line-height: 1.11px;
    display: flex;
  }

  .links-list {
    justify-content: flex-end;
    gap: 6px;
    display: flex;
  }

  .element-link {
    text-align: center;
    justify-content: center;
    gap: 14px;
  }

  .element-link.cart {
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .home-menu-link {
    cursor: pointer;
    background-color: var(--color-second-bg);
    color: var(--color-main-bg);
    border-radius: 30px;
    padding: 10px 20px;
    font-family: Neue Montreal, sans-serif;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 1.28571;
    display: flex;
  }

  .home-menu-link:hover, .home-menu-link:focus {
    background-color: var(--color-btn-green-dark);
  }

  .shopping-cart-link {
    fill: var(--color-main-bg);
    cursor: pointer;
    background-color: var(--color-second-bg);
    border-radius: 50px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    width: 38px;
    height: 38px;
    padding: 12px;
    display: flex;
  }

  .shopping-cart-link:hover, .shopping-cart-link:focus {
    background-color: var(--color-btn-green-dark);
  }

  .shopping-cart-icon {
    width: 14px;
  }

  .cart-text {
    display: none;
  }

  .header-logo-link {
    gap: 8px;
    padding: 12px 21px 13px 15px;
    display: flex;
  }

  .header-logo-link.link:focus, .header-logo-link.link:hover {
    background-color: var(--color-second-bg);
    border-radius: 9px;
  }

  .header-logo-link:hover .header-logo, .header-logo-link:focus .header-logo {
    color: var(--color-main-bg);
  }

  .header-logo-link:hover .header-logo-link, .header-logo-link:focus .header-logo-link {
    background-color: var(--color-main-bg);
    fill: var(--color-second-bg);
  }

  .header-logo-link:hover .icon-svg, .header-logo-link:focus .icon-svg {
    background-color: var(--color-main-bg);
  }

  .header-logo-link:hover .logo-icon, .header-logo-link:focus .logo-icon {
    fill: var(--color-second-bg);
  }
}

@media screen and (min-width: 767px) {
