.slit-in-vertical {
  animation: .45s ease-out both slit-in-vertical;
}

@keyframes slit-in-vertical {
  0% {
    opacity: 0;
    transform: translateZ(-800px)rotateY(90deg);
  }

  54% {
    opacity: 1;
    transform: translateZ(-160px)rotateY(87deg);
  }

  100% {
    transform: translateZ(0)rotateY(0);
  }
}

#dialogoverlay {
  opacity: .8;
  z-index: 10;
  background: #707070;
  width: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

#dialogbox {
  background: var(--color-second-bg);
  box-shadow: 0 4px 8px 0 var(--color-second-bg);
  z-index: 10;
  border-radius: 7px;
  width: 40%;
  margin: auto;
  transition: all .3s;
  display: none;
  position: absolute;
  left: 0;
  right: 0;
}

#dialogbox:hover {
  box-shadow: 0 8px 16px #585858e8;
}

.pure-material-button-contained {
  box-sizing: border-box;
  vertical-align: middle;
  text-align: center;
  text-overflow: ellipsis;
  text-transform: uppercase;
  color: rgb(var(--pure-material-onprimary-rgb, 255, 255, 255));
  background-color: var(--color-second-bg);
  cursor: pointer;
  border: none;
  border-radius: 4px;
  outline: none;
  min-width: 64px;
  height: 36px;
  padding: 0 16px;
  font-family: Neue Montreal, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 36px;
  transition: box-shadow .2s;
  display: inline-block;
  position: relative;
  overflow: hidden;
  box-shadow: 0 3px 1px -2px #0003, 0 2px 2px #00000024, 0 1px 5px #0000001f;
}

.pure-material-button-contained::-moz-focus-inner {
  border: none;
}

.pure-material-button-contained:before {
  content: "";
  background-color: rgb(var(--pure-material-onprimary-rgb, 255, 255, 255));
  opacity: 0;
  transition: opacity .2s;
  position: absolute;
  inset: 0;
}

.pure-material-button-contained:after {
  content: "";
  background-color: rgb(var(--pure-material-onprimary-rgb, 255, 255, 255));
  opacity: 0;
  border-radius: 50%;
  width: 32px;
  height: 32px;
  padding: 50%;
  transition: opacity 1s, transform .5s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.pure-material-button-contained:hover, .pure-material-button-contained:focus {
  box-shadow: 0 2px 4px -1px #0003, 0 4px 5px #00000024, 0 1px 10px #0000001f;
}

.pure-material-button-contained:hover:before {
  opacity: .08;
}

.pure-material-button-contained:focus:before {
  opacity: .24;
}

.pure-material-button-contained:hover:focus:before {
  opacity: .3;
}

.pure-material-button-contained:active {
  box-shadow: 0 5px 5px -3px #0003, 0 8px 10px 1px #00000024, 0 3px 14px 2px #0000001f;
}

.pure-material-button-contained:active:after {
  opacity: .32;
  transition: transform;
  transform: translate(-50%, -50%)scale(0);
}

.pure-material-button-contained:disabled {
  color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), .38);
  background-color: rgba(var(--pure-material-onsurface-rgb, 0, 0, 0), .12);
  box-shadow: none;
  cursor: initial;
}

.pure-material-button-contained:disabled:before, .pure-material-button-contained:disabled:after {
  opacity: 0;
}

